<template>
  <h3>All users</h3>

  <router-link to="/register">Register user</router-link>

  <table>
    <tr>
      <th>Email</th>
      <th>Last name</th>
      <th>First name</th>
      <th>Role</th>
      <th>Actions</th>
    </tr>
    <tr v-for="user in users" :key="user">
      <td>{{ user.emailAddress }}</td>
      <td>{{ user.lastName }}</td>
      <td>{{ user.firstName }}</td>
      <td>{{ user.role }}</td>
      <td><button @click="deleteUser(user)">Delete</button></td>
    </tr>
  </table>
</template>
<script>
import { authComputed } from "@/store/helpers";
import axios from "axios";
export default {
  name: "HomeView",
  methods: {
    deleteUser(user) {
      axios
        .delete("http://localhost:8001/user", {
          params: {
            email: user.emailAddress,
          },
        })
        .then((response) => {
          this.users = response.data;
        })
        .catch((err) => {
          alert(err.message);
        });
    },
  },
  components: {},
  created() {
    document.title = "QS99 - Students";
    //getting subjects of the user
    axios.get("http://localhost:8001/user/getAllUsers").then((response) => {
      this.users = response.data;
    });
  },
  data() {
    return {
      users: null,
    };
  },
  computed: {
    ...authComputed,
  },
};
</script>
<style scoped>
table {
  margin: auto;
  width: 80%;
  border-collapse: collapse;
}

td,
th {
  border: 1px solid #999;
  padding: 0.5rem;
  text-align: left;
}
</style>
